import axios from 'axios';
import React, { ChangeEvent, useEffect, useState } from 'react';
import { useHistory, useRouteMatch } from 'react-router-dom';
import { DocumentType } from '../../../contracts/DocumentType';
import { handleError, outIf } from '../../../helpers';
import toastr from 'toastr';

export function Form() {
	const [processing, setProcessing] = useState(false);
	const [mode, setMode] = useState('Add');
	const [data, setData] = useState<DocumentType>({
		id: -1,
		name: '',
	});

	const history = useHistory();

	const handleChange = (e: ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
		const { id, value } = e.target;
		setData({
			...data,
			[id]: value,
		});
	};

	const reset = () => setData({ name: '', id: -1 });

	const submit = async () => {
		if (processing) return;
		setProcessing(true);
		try {
			await (mode === 'Add' ? axios.post<DocumentType>('/document-types', data) : axios.put<DocumentType>('/document-types', data));
			toastr.success('Document Type saved successfully.');
			reset();
		} catch (error) {
			handleError(error);
		} finally {
			setProcessing(false);
		}
	};

	const fetchDocumentType = async (documentTypeID: string) => {
		setMode('Edit');
		try {
			const { data } = await axios.get<DocumentType>(`/document-types/show?id=${documentTypeID}`);
			setData(data);
		} catch (error) {
			handleError(error);
			history.goBack();
		}
	};

	const { params, path } = useRouteMatch<{ id: string }>();

	useEffect(() => {
		if (path.includes('edit')) {
			fetchDocumentType(params.id);
		}
		// eslint-disable-next-line
	}, []);

	return (
		<form
			className='container'
			onSubmit={async (e) => {
				e.preventDefault();
				await submit();
			}}>
			<div className='d-flex'>
				<h3 className='align-self-center mb-0'>{mode} Document Type</h3>
				<button
					className='btn btn-info btn-sm align-self-center ml-auto'
					onClick={(e) => {
						e.preventDefault();
						history.goBack();
					}}>
					Back
				</button>
			</div>
			<div className='form-group'>
				<label htmlFor='name'>Name</label>
				<input
					type='text'
					name='name'
					id='name'
					placeholder='Name'
					className={`form-control form-control-sm ${outIf(processing, 'disabled')}`}
					disabled={processing}
					value={data.name}
					onChange={handleChange}
				/>
			</div>
			<div className='form-group'>
				<button type='submit' className={`btn btn-primary btn-sm ${outIf(processing, 'disabled')}`} disabled={processing}>
					{processing ? (
						<div className='spin'>
							<i className='bi bi-arrow-clockwise'></i>
						</div>
					) : (
						'Save'
					)}
				</button>
			</div>
		</form>
	);
}
