import axios from 'axios';
import React, { useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import { User } from '../contracts/User';
import { handleError, makeMask, outIf } from '../helpers';
import { routes } from '../routes';
import state from '../state';
import styles from '../styles/Login.module.css';

type Response = {
	user: User;
	token: string;
};

export function Register() {
	const [data, _setData] = useState({
		name: '',
		email: '',
		password: '',
	});
	const [processing, setProcessing] = useState(false);

	const setData = makeMask(_setData, (data: any) => ({ ...data }));

	const history = useHistory();

	if (state.has('logged') && state.get<boolean>('logged')) {
		history.push(routes.DASHBOARD);
	}

	const submit = async () => {
		setProcessing(true);
		try {
			const response = await axios.post<Response>('/auth/register', data);

			const { user, token } = response.data;

			state.set('logged', true);
			state.set('user', user);
			state.set('token', token);
			history.push(routes.DASHBOARD);
		} catch (error) {
			handleError(error);
			console.log(error);
		} finally {
			setProcessing(false);
		}
	};

	return (
		<div className={`w-100 h-100vh d-flex ${styles.body}`}>
			<div className='align-self-center mx-auto'>
				<div className={`card shadow ${styles.card}`}>
					<div className='card-header text-center'>
						<img src='https://via.placeholder.com/200' alt='Logo' className='img-fluid hw-max-75 rounded-circle' />
						<h2 className='card-title'>DTMS</h2>
						<p className='card-text'>🍀 New here? Please fill in the required information.</p>
					</div>
					<div className='card-body'>
						<form
							onSubmit={(e) => {
								e.preventDefault();
								submit();
							}}>
							<div className='form-group'>
								<label htmlFor='name'>Name</label>
								<input
									type='text'
									name='name'
									id='name'
									placeholder='John Smith'
									className={`form-control form-control-sm ${outIf(processing, 'disabled')}`}
									disabled={processing}
									value={data.name}
									onChange={(e) => {
										data.name = e.target.value;
										setData(data);
									}}
								/>
							</div>
							<div className='form-group'>
								<label htmlFor='email'>Email</label>
								<input
									type='text'
									name='email'
									id='email'
									placeholder='email@dtms.com'
									className={`form-control form-control-sm ${outIf(processing, 'disabled')}`}
									disabled={processing}
									value={data.email}
									onChange={(e) => {
										data.email = e.target.value;
										setData(data);
									}}
								/>
							</div>
							<div className='form-group'>
								<label htmlFor='password'>Password</label>
								<input
									type='password'
									name='password'
									id='password'
									placeholder='Password'
									className={`form-control form-control-sm ${outIf(processing, 'disabled')}`}
									disabled={processing}
									value={data.password}
									onChange={(e) => {
										data.password = e.target.value;
										setData(data);
									}}
								/>
							</div>
							<div className='form-group'>
								<button
									type='submit'
									className={`btn btn-sm btn-secondary ${outIf(processing, 'disabled')}`}
									disabled={processing}>
									{processing ? (
										<div className='spin'>
											<i className='bi bi-arrow-clockwise'></i>
										</div>
									) : (
										'Register'
									)}
								</button>
							</div>
							<div className='form-group'>
								Already have an account?{' '}
								<Link to={routes.LOGIN} className='d-inline'>
									Login
								</Link>
							</div>
						</form>
					</div>
				</div>
			</div>
		</div>
	);
}
